@keyframes fall {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
  }
  50% {
    transform: translateY(50vh) translateX(10px) rotate(180deg); 
    opacity: 0.8;
  }
  100% {
    transform: translateY(110vh) rotate(360deg);
    opacity: 0;
  }
}


.wrapper{
  width: 850px;
  height: 550px;
  border: 1px solid lightgrey;
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.546);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.container{
  padding: 50px 40px;
}

.content__wrapper{
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

.title__wrapper{
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 8px;
  color: rgba(0, 0, 0, 0.678)
}

h1{
  margin: 0;
  font-size: 48px;
  letter-spacing: 1.5px;
}

h2{
  letter-spacing: 1.5px;
  font-size: 32px;
  font-style: italic;
}

.emphasis{
  color: rgba(255, 0, 0, 0.71)  
}

.giphy__img{
  height: 250px;
  width: 250px;
}


.buttons__wrapper{
  display: flex;
  gap: 60px;
  width: 100%;
  max-width: 100%;
  justify-content: center;
}

.button__no{
  padding:10px 40px;
  font-size: 24px;
  width: 20%;
  font-size: 22px;
  background-color: white;
  transform: background-color 0.3s ease;
  border: 1px solid rgba(128, 128, 128, 0.87);
}




.button:hover{
  cursor:pointer;
  background-color: pink;
  border:none;
}

.button__no:hover{
  opacity: 0;
  cursor: not-allowed;
}


.img__wrapper{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  margin: 0 auto 20px;
  /* box-shadow: rgba(0, 0, 0, 0.12) 0px 4px 10px; */
  z-index: 1;
  border: none;
}

/* center when the wrapper is a direct child of .home */
.home > .img__wrapper{
  margin: 0;
}

.otter__img{
  max-width: 400px;
  width: 100%;
  height: 575px;
  border-radius: 20px;
  display: block;
  opacity: 0;
  transform: translateY(12px) scale(0.80);
  transition: opacity 800ms cubic-bezier(.2,.9,.2,1), transform 800ms cubic-bezier(.2,.9,.2,1);
}

.otter__img.visible{
  opacity: 1;
  transform: translateY(0) scale(1);
}

.text{
  opacity: 0;
  margin-bottom: 12px;
  font-size: 48px;
  color: rgba(0, 0, 0, 0.68);
  transition: opacity 800ms ease-in-out;
  margin-top: 12px;
}

.text.visible{
  opacity: 1;
}

a {
  position: relative;
  width: 275px;
  height: 50px;
  transition: 4s;
  transform-style: preserve-3d;
  transform: perspective(1000px) rotateX(0deg);
}

a:hover {
  transform: perspective(1000px) rotateX(360deg) scale(4.0);
}
a span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;
  background: rgba(255, 255, 255, 0.986);
  text-transform: uppercase;
  font-size: 22px;
  transition: 0.5s;
  border: 1px solid grey;
  box-sizing: border-box;
  box-shadow: insert 0 20px 50px rgba(0, 0, 0, 0.2);

}

a:hover span {
  color: #fff;
  background: pink;
}
a span:nth-child(1) {
  transform: rotateX(360deg) translateZ(25px);
}
a span:nth-child(2) {
  transform: rotateX(270deg) translateZ(25px);
}
a span:nth-child(3) {
  transform: rotateX(180deg) translateZ(25px);
}
a span:nth-child(4) {
  transform: rotateX(90deg) translateZ(25px);
}

@media screen and (max-width: 393px){
    html, body, #root{
      height: 852px;
      overflow: hidden;
    }
    .home{
      overflow:hidden;
    }
    .wrapper{
      width: 325px;
      height: 600px;
    }
    .container{
      height: 100%;
      padding: 40px 40px;
    }
    .content__wrapper{
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .giphy__img{
      height: 215px;
      width: 215px;
    }
    .buttons__wrapper{
      display: flex;
      flex-direction: column;
      gap: 24px;
      max-width: none;
      width: auto;
    }
    .button__no{
      width: 100%;
      color:#000;
    }
    a:hover {
      transform: perspective(1000px) rotateX(360deg) scale(1.1);
    }
    .img-text__wrapper{
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    .text{
      margin-bottom: 24px;
    }
    .otter__img{
      height: 485px;
    }
}
  

